<template>
  <div class='counseling'>
    <div class='header'>
      咨询信息
    </div>

    <el-table
      :data='tableData'
      style='width: 100%'>
      <template v-for='(item,index) in tableHead'>
        <el-table-column
          v-if='item.prop!="content"&&item.prop!="feedback"'
          :prop='item.prop'
          :label='item.title'
          :min-width='item.minWidth'
        align='center'>
        </el-table-column>
        <el-table-column
          v-else-if='item.prop=="content"'
          :prop='item.prop'
          :label='item.title'
          :min-width='item.minWidth'
          align='center'
        >
          <template slot-scope='scope'>
            <el-button type='text' @click='goPlay(scope.row,1)'>咨询语音</el-button>
          </template>
        </el-table-column>
        <el-table-column
          v-else-if='item.prop=="feedback"'
          :prop='item.prop'
          :label='item.title'
          :min-width='item.minWidth'
          align='center'
        >
          <template slot-scope='scope'>
            <el-button type='text' @click='goPlay(scope.row,2)'>回复语音</el-button>
          </template>
        </el-table-column>
      </template>
    </el-table>


    <el-dialog
      :title="type==1?'咨询内容':'咨询回复'"
      :visible.sync='showDialog'
      width='30%'
      center
      @close='close'>
      <div class='counseling-content'>
        <!--   波动图   -->
        <!--   时长-->
        <span class='duration'>语音时长：{{ play_item.duration }}s</span>
      </div>
      <span slot='footer' class='dialog-footer'>
       <el-button type='primary' @click='startPlay' v-if='play_status===0'>开始播放</el-button>
       <el-button type='primary' @click='pausePlay' v-if='play_status===1||play_status===3'>暂停播放</el-button>
       <el-button type='primary' @click='resumePlay' v-if='play_status===2'>继续播放</el-button>
<!--       <el-button @click='stopPlay' v-if='play_status!==4'>结束播放</el-button>-->
      </span>
    </el-dialog>
  </div>
</template>

<script>

import Recorder from 'js-audio-recorder';
import mp3 from './张杰 - 勿忘心安.mp3'
export default {
  name: 'list',
  data() {
    return {
      tableHead: [
        { title: '咨询时间', prop: 'ask_time', minWidth: 100 },
        { title: '咨询内容', prop: 'content', minWidth: 100 },
        { title: '心理教师', prop: 'teacher', minWidth: 100 },
        { title: '咨询回复', prop: 'feedback', minWidth: 100 },
        { title: '回复时间', prop: 'feedback_time', minWidth: 100 }

      ],
      tableData: [
        {ask_time:'2023-5-5',content:'',teacher:'张之',feedback:'',feedback_time:'2023-5-5',duration: ''},

      ],
      type:1,//播放类型 1 咨询内容 2 咨询回复
      showDialog:false,
      play_item:{
        duration:0,
        content:'',
        feedback:''
      },
      play_status: 0, //1 开始 2 暂停  3 继续  4 结束
      audio:null
    };
  },
  methods:{
    close(){

    },
    goPlay(item,type){
      this.type = type
      //mock
      // this.play_item = item
      // this.showDialog = true
      //
      // this.audio = new Audio(mp3)
      // this.audio.play()

      window.open('http://xwcs.xyruxue.com//uploads/recruit/2023-04-25-12/17e0ba2e0a7c1789038d08da8613457b.mp3','_blank')
    },
    //开始播放
    startPlay() {
      this.audio.play()
      this.play_status = 1
    },
    //停止播放
    pausePlay() {
      console.log('停止')
      this.audio.pause();
      this.play_status = 2;
    },
    //继续播放
    resumePlay() {
      this.audio.play();
      this.play_status = 3;
    },
    //结束播放
    stopPlay() {
      this.audio.ended();
      this.play_status = 4;
      this.cancelPlay()
    },
    //取消播放
    cancelPlay() {
      this.showDialog = false;
      this.play_status = 0;
    },
  }
};
</script>

<style scoped>
.counseling {
  height: 100%;
}

.header {
  text-align: center;
  color: #fff;
  background: #2a33e6;
  width: 100%;
  padding: 10px 0;
}

</style>



